.about-me-section {
  padding-top: 35px;
  padding-bottom: 32px;

  @media screen and (min-width: $tablet) {
    padding-top: 80px;
    padding-bottom: 80px;
  }

  @media screen and (min-width: $laptop) {
    padding-top: 180px;
  }

  @media screen and (min-width: $desktop) {
    padding-top: 223px;
  }
}

.about-me-section__section-title {
  position: relative;

  margin-bottom: 21px;

  @media screen and (min-width: $tablet) {
    margin-bottom: 55px;
  }
}

.about-me-section__text {
  position: relative;
}

.about-me-section__icon {
  position: absolute;
  top: -14px;
  right: -22.5px;

  fill: var(--secondary-color);

  @media screen and (min-width: $tablet) {
    top: -16px;
    right: -30px;
  }

  @media screen and (min-width: $laptop) {
    top: -27px;
    right: -38px;
  }
}

.about-me-content {
  &--desktop {
    display: none;
  }

  @media screen and (min-width: $tablet) {
    &--mobile {
      display: flex;
      align-items: center;
      gap: 40px;
    }
  }

  @media screen and (min-width: $laptop) {
    &--mobile {
      display: none;
    }

    &--desktop {
      display: flex;
      align-items: center;
      gap: 20px;
    }
  }
}

.about-me-content__thumb {
  margin-bottom: 38px;

  max-width: 335px;
  max-height: 503px;

  @media screen and (min-width: $tablet) {
    flex-shrink: 0;
    margin-bottom: 0;

    max-width: 400px;
    max-height: 619px;
  }

  @media screen and (min-width: $laptop) {
    position: relative;

    border-radius: 5px;
    overflow: hidden;
  }
}

.about-me-content__img {
  display: block;
  width: 100%;
  height: 100%;

  object-fit: cover;
  object-position: center;
}

.about-me-content__title {
  margin-bottom: 34px;

  font-weight: 600;
  font-size: 18px;
  line-height: 1.22;
  text-align: center;
  letter-spacing: 0.04em;

  @media screen and (min-width: $tablet) {
    font-size: 20px;
    letter-spacing: 0.04em;
  }

  @media screen and (min-width: $laptop) {
    margin-bottom: 5px;

    font-size: 18px;
    letter-spacing: 0.04em;
  }
}

.about-me-content__label {
  display: block;
  margin-bottom: 29px;

  font-weight: 600;
  font-size: 14px;
  line-height: 1.21;
  text-align: center;
  letter-spacing: 0.04em;

  @media screen and (min-width: $desktop) {
    margin-bottom: 33px;
  }
}

.about-me-content__line {
  position: relative;

  display: inline-block;

  &::after {
    content: '';

    position: absolute;
    left: 0;
    bottom: -16px;

    display: block;
    width: 100%;
    height: 1px;

    background-color: var(--primary-color);

    @media screen and (min-width: $laptop) {
      bottom: -14px;
    }

    @media screen and (min-width: $desktop) {
      bottom: -16px;
    }
  }
}

.list-facts__item:not(:last-child) {
  margin-bottom: 16px;
}

.list-facts__text {
  font-weight: 600;
  font-size: 14px;
  line-height: 1.21;
  text-align: center;

  @media screen and (min-width: $tablet) {
    font-size: 16px;
  }

  @media screen and (min-width: $laptop) {
    font-size: 13px;
  }

  @media screen and (min-width: $desktop) {
    font-size: 14px;
  }
}

.list-facts__accent {
  color: var(--accent-color);
}

.about-me-overlay {
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  &:hover.overlay-first-img,
  &:focus.overlay-first-img {
    opacity: 1;
  }

  &:hover.overlay-second-img,
  &:focus.overlay-second-img {
    opacity: 1;
  }

  &:hover.overlay-third-img,
  &:focus.overlay-third-img {
    opacity: 1;
  }
}

.overlay-first-img {
  padding: 48px 40px;

  background-image: linear-gradient(
    0deg,
    rgba(0, 0, 0, 0) 62.45%,
    rgba(0, 0, 0, 0.9) 114.8%
  );
  opacity: 0;

  transition: opacity var(--duration-overlay) var(--timing-function);
}

.overlay-first-img__text {
  font-weight: 600;

  &--first {
    font-size: 30px;
    line-height: 1.23;
    letter-spacing: 0.04em;
  }

  &--second {
    font-size: 21px;
    line-height: 1.24;
    letter-spacing: 0.04em;
  }
}

.overlay-second-img {
  padding: 32px 40px 40px 32px;

  backdrop-filter: blur(23px);
  opacity: 0;

  transition: opacity var(--duration-overlay) var(--timing-function);

  @media screen and (min-width: $desktop) {
    padding: 38px 46px 46px 32px;
  }
}

.overlay-third-img {
  display: flex;
  align-items: end;
  gap: 23px;
  padding: 49px 14px;

  background-image: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0) 50.38%,
    rgba(0, 0, 0, 0.9) 97.09%
  );
  opacity: 0;

  transition: opacity var(--duration-overlay) var(--timing-function);

  @media screen and (min-width: $desktop) {
    gap: 35px;
  }
}

.overlay-third-img__text {
  font-weight: 600;
  font-size: 12px;
  line-height: 1.26;
  text-align: center;

  &:nth-child(1) {
    position: relative;

    &::after {
      content: '';

      position: absolute;
      right: -12px;
      top: 50%;

      display: block;
      width: 1px;
      height: 92px;

      background-color: var(--primary-color);
      transform: translateY(-50%);

      @media screen and (min-width: $desktop) {
        right: -17px;
      }
    }
  }
}

.overlay-third-img__icon-box {
  position: relative;

  &::after {
    content: '';
    position: absolute;
    left: 4px;
    bottom: -8px;

    width: 67px;
    height: 14px;

    background-image: url('../images/lines.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url('../images/lines@2x.png');
    }
  }
}
