.page-footer {
  padding: 42px 0;
}

.page-address {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.page-address__email {
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 2;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  text-decoration: none;
  color: var(--primary-text-color);

  &--active {
    color: var(--secondary-color);
  }
}

.contact-list {
  display: flex;
  gap: 27px;
  margin-bottom: 22px;

  @media screen and (min-width: $tablet) {
    margin-bottom: 0;
  }
}

.contact-list__link {
  display: flex;
  align-items: center;
  justify-content: center;

  color: var(--primary-text-color);

  &--active {
    color: var(--secondary-color);
  }
}

.contact-list__icon {
  fill: currentColor;
}
