.feature-section {
  padding-top: 40px;
  padding-bottom: 50px;

  @media screen and (min-width: $tablet) {
    padding-top: 90px;
    padding-bottom: 90px;
  }

  @media screen and (min-width: $laptop) {
    padding-top: 115px;
  }

  & .button--secondary {
    font-size: 17px;
    line-height: 1.24;
  }
}

.feature-section__section-title {
  margin-bottom: 40px;

  color: var(--primary-text-color);

  @media screen and (min-width: $tablet) {
    margin-bottom: 50px;
  }

  & > span {
    position: relative;

    &::after {
      content: '';

      display: none;

      @media screen and (min-width: $tablet) {
        position: absolute;
        top: -32px;
        right: -36px;

        display: block;
        width: 51px;
        height: 65px;

        background-image: url('../images/crown.svg');
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
      }

      @media screen and (min-width: $tablet) {
        top: -26px;
        right: -33px;
      }
    }
  }
}

.feature-list {
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  margin-bottom: 60px;

  @media screen and (min-width: $tablet) {
    gap: 50;
    flex-wrap: nowrap;
    margin-bottom: 0;
  }
}

.feature-list__title {
  margin-bottom: 8px;

  font-weight: 700;
  font-size: 30px;
  line-height: 1.23;
  text-align: center;
  letter-spacing: 0.04em;
  color: var(--secondary-color);

  @media screen and (min-width: $tablet) {
    margin-bottom: 10px;

    text-align: start;
  }

  @media screen and (min-width: $laptop) {
    margin-bottom: 10px;

    font-size: 35px;
    text-align: start;
  }
}

.feature-list__descr {
  font-weight: 500;
  font-size: 19px;
  line-height: 1.21;
  text-align: center;
  color: var(--primary-text-color);

  @media screen and (min-width: $tablet) {
    text-align: start;
  }
}
